.app__container {
  width: 90vw;
  max-width: 50rem;
  margin: 100px auto;
  height: 20rem; 
  display: flex;
  flex-direction: column;
  position: relative;
}

.app__tabsContainer {
  display: flex;
  align-items: center;
  overflow-y: hidden;
  overflow-x: scroll;

  /* width: 100%;
  height: 3rem; */
  padding: .25rem;
}

.app__tabsContainer::-webkit-scrollbar {
  display: none;
}

.app__plusIcon,
.app__chevronLeft,
.app__chevronRight {
  font-size: 20px;
  position: absolute;
}

.app__chevronLeft {
  right: -30px;
}

.app__chevronRight {
  right: -45px;
}

.app__plusIcon {
  right: -70px;
}

.icon:hover {
  transform: scale(1.2);
  transition: transform .2s ease;
}

.app__contentContainer > p {
  padding: 2rem;
  color: tomato;
  font-size: 3rem;
}